/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable import/no-anonymous-default-export */
import React, { useEffect, useState } from 'react';
import { collectionApi, seasonApi } from '../api';
import styled from 'styled-components';
import Loader from '../Components/Loader';
import Message from '../Components/Message';
import { Helmet } from 'react-helmet';
import { Rating } from '@material-ui/lab';
import { Link } from 'react-router-dom';

const Container = styled.div`
    height: calc(100vh-50px);
    width: 100vw;
`;

const Backdrop = styled.div`
    position: fixed;
    top: 50px;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(${(props) =>
        `https://image.tmdb.org/t/p/original${props.bdUrl}`});
    background-position: center center;
    background-size: cover;
    filter: blur(1.5px);
    opacity: 0.5;
    z-index: -2;
`;
const Content = styled.div`
    display: flex;
    height: 100vh;
    width: 100%;
    position: relative;
`;
const CoverContainer = styled.div``;
const Cover = styled.div`
    display: block;
    position: fixed;
    top: 70px;
    left: 30px;
    height: 450px;
    width: 300px;
    background-image: url(${(props) => props.poUrl});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
`;

const Data = styled.div`
    width: 1200px;
    height: 700px;
    margin-left: 350px;
`;

const Title = styled.span`
    font-size: 50px;
    font-weight: 600;
    color: white;
`;

const ItemContainer = styled.div`
    display: flex;
`;

const Item = styled.span`
    display: flex;
    align-items: center;
`;

const Divider = styled.div`
    margin: 0px 10px;
`;

const Overview = styled.p`
    font-size: 12px;
    opacity: 0.9;
    line-height: 1.5;
    width: 80%;
`;

const Contents = styled.div`
    display: grid;
    align-items: center;
    grid-auto-flow: row;
    grid-auto-columns: 100%;
    overflow: auto;
`;

const ContentCover = styled.div`
    height: 180px;
    width: 120px;
    background-image: url(${(props) => props.profile});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
    margin-bottom: 5px;
`;

const ContentCoverH = styled.div`
    height: 140px;
    width: 210px;
    background-image: url(${(props) => props.profile});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
    margin-bottom: 5px;
`;

const CTContainer = styled.div`
    width: 100%;
`;

const ContentName = styled.div`
    font-size: 18px;
    font-weight: 600;
    height: 18px;
    margin: 10px 0px;
    padding: 0px;
`;

const ContentOverview = styled.div`
    font-size: 14px;
    height: 18px;
    margin: 0px;
    padding: 0px;
`;

const ContentContainer = styled.div`
    &:hover {
        ${ContentCover} {
            opacity: 0.5;
        }
    }
    display: flex;
`;

export default (props) => {
    const {
        location: { pathname },
        match: {
            params: { id, number },
        },
        history: { push },
    } = props;
    const [result, setResult] = useState(null);
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(true);
    const isCollection = pathname.includes('/collection');

    const viewDetail = async () => {
        const parsedId = parseInt(id);
        const parsedNumber = parseInt(number);
        if (isNaN(parsedId)) {
            return push('/');
        }

        let result = null;

        try {
            if (isCollection) {
                console.log(isCollection);
                const request = await collectionApi.detail(parsedId);
                result = request;
            } else {
                console.log(isCollection);
                const request = await seasonApi.detail(parsedId, parsedNumber);
                console.log(request);
                result = request;
            }
        } catch {
            setError("Can't find anything.");
        } finally {
            setResult(result);
            setLoading(false);
        }
    };

    useEffect(() => {
        setLoading(true);
        viewDetail();
    }, []);

    return loading ? (
        <Loader>
            <title>Detail | OBSOS</title>
        </Loader>
    ) : (
        <Container>
            <Helmet>
                <title>
                    {`${
                        result != null &&
                        (result.data.title != null
                            ? result.data.title
                            : result.data.name)
                    }`}
                </title>
            </Helmet>
            <Backdrop bdUrl={result != null && result.data.backdrop_path} />
            {error ? <Message text={error} color="tomato" /> : null}
            <Content>
                <CoverContainer>
                    <Cover
                        poUrl={
                            result != null && result.data.poster_path != null
                                ? `https://image.tmdb.org/t/p/w300${result.data.poster_path}`
                                : 'data:image/png;base64,'
                        }
                    />
                </CoverContainer>
                <Data>
                    <Title>
                        {result != null &&
                            (result.data.title != null
                                ? result.data.title
                                : result.data.name != null
                                ? result.data.name
                                : null)}
                    </Title>

                    <Overview>
                        {result != null &&
                            result.data != null &&
                            (result.data.overview != null
                                ? result.data.overview
                                : null)}
                    </Overview>
                    <Contents>
                        {result != null &&
                        result.data != null &&
                        result.data.parts != null
                            ? result.data.parts.map((content) => {
                                  return (
                                      <Link
                                          to={
                                              content.title != null
                                                  ? `/movie/${content.id}`
                                                  : `/show/${content.id}`
                                          }
                                      >
                                          <ContentContainer>
                                              <ContentCover
                                                  profile={
                                                      content.poster_path !=
                                                      null
                                                          ? `https://image.tmdb.org/t/p/w200${content.poster_path}`
                                                          : 'data:image/png;base64,'
                                                  }
                                              />
                                              <CTContainer>
                                                  <ContentName>
                                                      {content.name != null
                                                          ? content.name
                                                          : null}
                                                      {content.title != null
                                                          ? content.title
                                                          : null}
                                                  </ContentName>
                                                  <ItemContainer>
                                                      <Item>
                                                          {content != null &&
                                                              (content.release_date !=
                                                              null
                                                                  ? content.release_date.substring(
                                                                        0,
                                                                        4
                                                                    )
                                                                  : content.first_air_date !=
                                                                    null
                                                                  ? content.first_air_date.substring(
                                                                        0,
                                                                        4
                                                                    )
                                                                  : 'Not yet')}
                                                      </Item>

                                                      <Divider>|</Divider>
                                                      <Item>
                                                          <Rating
                                                              value={
                                                                  content.vote_average
                                                              }
                                                              max={10}
                                                              precision={0.1}
                                                              readOnly
                                                              size="small"
                                                          />
                                                          &nbsp;
                                                          {content != null &&
                                                              (content.vote_average !=
                                                              null
                                                                  ? content.vote_average
                                                                  : null)}
                                                          &nbsp;/ 10
                                                      </Item>
                                                  </ItemContainer>
                                                  <ContentOverview>
                                                      {content.overview != null
                                                          ? content.overview
                                                          : null}
                                                  </ContentOverview>
                                              </CTContainer>
                                          </ContentContainer>
                                      </Link>
                                  );
                              })
                            : null}
                        {result != null &&
                        result.data != null &&
                        result.data.episodes != null
                            ? result.data.episodes.map((content) => {
                                  return (
                                      <ContentContainer>
                                          <ContentCoverH
                                              profile={
                                                  content.still_path != null
                                                      ? `https://image.tmdb.org/t/p/w200${content.still_path}`
                                                      : 'data:image/png;base64,'
                                              }
                                          />
                                          <CTContainer>
                                              <ContentName>
                                                  {content.name != null
                                                      ? content.name
                                                      : null}
                                                  {content.title != null
                                                      ? content.title
                                                      : null}
                                              </ContentName>
                                              <ItemContainer>
                                                  <Item>
                                                      {content != null &&
                                                          (content.release_date !=
                                                          null
                                                              ? content.release_date.substring(
                                                                    0,
                                                                    4
                                                                )
                                                              : content.air_date !=
                                                                null
                                                              ? content.air_date
                                                              : 'Not yet')}
                                                  </Item>

                                                  <Divider>|</Divider>
                                                  <Item>
                                                      <Rating
                                                          value={
                                                              content.vote_average
                                                          }
                                                          max={10}
                                                          precision={0.1}
                                                          readOnly
                                                          size="small"
                                                      />
                                                      &nbsp;
                                                      {content != null &&
                                                          (content.vote_average !=
                                                          null
                                                              ? content.vote_average
                                                              : null)}
                                                      &nbsp;/ 10
                                                  </Item>
                                              </ItemContainer>
                                              <ContentOverview>
                                                  {content.overview != null
                                                      ? content.overview
                                                      : null}
                                              </ContentOverview>
                                          </CTContainer>
                                      </ContentContainer>
                                  );
                              })
                            : null}
                    </Contents>
                </Data>
            </Content>
        </Container>
    );
};
