/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable import/no-anonymous-default-export */
import React, { useEffect, useState } from 'react';
import { peopleApi } from '../api';
import styled from 'styled-components';
import Loader from '../Components/Loader';
import Message from '../Components/Message';
import { Helmet } from 'react-helmet';

import { Link } from 'react-router-dom';

const Container = styled.div`
    height: calc(100vh-50px);
    width: 100vw;
`;

const Content = styled.div`
    display: flex;
    width: 100%;
    position: relative;
`;
const CoverContainer = styled.div``;
const Cover = styled.div`
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 450px;
    width: 300px;
    background-image: url(${(props) =>
        `https://image.tmdb.org/t/p/w300${props.poUrl}`});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
`;

const Data = styled.div`
    width: 80vw;
    margin-left: 350px;
`;

const Title = styled.span`
    font-size: 50px;
    font-weight: 600;
    color: white;
`;

const ItemContainer = styled.div`
    display: flex;
`;

const Item = styled.span`
    display: flex;
    align-items: center;
`;

const Divider = styled.div`
    margin: 0px 10px;
`;

const Overview = styled.p`
    font-size: 12px;
    opacity: 0.9;
    line-height: 1.5;
    width: 80%;
`;

const SectionTitle = styled.span`
    display: block;
    font-size: 20px;
    margin-top: 20px;
`;

const HR = styled.hr`
    margin: 10px 0px;
`;

const Credits = styled.div`
    height: 270px;
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    grid-auto-columns: 8%; // play with this to change height of the children, 50% will fill half
    // grid-template-columns: unset; // do not set template columns and rows
    // grid-template-rows: unset;
    overflow-x: auto;
`;

const CreditCover = styled.div`
    height: 150px;
    width: 100px;
    background-image: url(${(props) => props.poster});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
    margin-bottom: 5px;
`;
const CreditTitle = styled.div`
    font-size: 12px;
    height: 18px;
    margin: 0px;
    padding: 0px;
`;

const CharacterName = styled.div`
    font-size: 12px;
    color: red;
    height: 18px;
    margin: 0px;
    padding: 0px;
`;
const CreditContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    text-align: center;
    &:hover {
        ${CreditCover} {
            opacity: 0.5;
        }
        ${CreditTitle} {
            font-size: 14px;
            font-weight: 600;
        }
        ${CharacterName} {
            font-size: 14px;
            font-weight: 600;
        }
    }
`;

export default (props) => {
    const [result, setResult] = useState(null);
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(true);
    const [movieCredits, setMovieCredits] = useState(null);
    const [tvCredits, setTvCredits] = useState(null);
    const {
        match: {
            params: { id },
        },
        history: { push },
    } = props;

    const viewDetail = async () => {
        const parsedId = parseInt(id);
        if (isNaN(parsedId)) {
            return push('/');
        }

        let result = null;
        let tvCredits = null;
        let movieCredits = null;

        try {
            const request = await peopleApi.peopleDetail(parsedId);
            result = request.data;
            const tvRequest = await peopleApi.tvCredits(parsedId);
            tvRequest.data.cast = tvRequest.data.cast.sort((a, b) => {
                return b.popularity - a.popularity;
            });
            tvRequest.data.crew = tvRequest.data.crew.sort((a, b) => {
                return b.popularity - a.popularity;
            });

            tvCredits = tvRequest.data;
            const movieRequest = await peopleApi.movieCredits(parsedId);
            movieRequest.data.cast = movieRequest.data.cast.sort((a, b) => {
                return b.popularity - a.popularity;
            });
            movieRequest.data.crew = movieRequest.data.crew.sort((a, b) => {
                return b.popularity - a.popularity;
            });
            movieCredits = movieRequest.data;
        } catch {
            setError("Can't find anything.");
        } finally {
            setResult(result);
            setMovieCredits(movieCredits);
            setTvCredits(tvCredits);
            setLoading(false);
        }
    };
    useEffect(() => {
        setLoading(true);
        viewDetail();
    }, [id]);

    return loading ? (
        <Loader>
            <title>Profile | OBSOS</title>
        </Loader>
    ) : (
        <Container>
            <Helmet>
                <title>
                    {`${
                        result != null &&
                        (result.title != null ? result.title : result.name)
                    }`}
                </title>
            </Helmet>

            {error ? <Message text={error} color="tomato" /> : null}
            <Content>
                <CoverContainer>
                    <Cover poUrl={result != null && result.profile_path} />
                </CoverContainer>
                <Data>
                    <Title>
                        {result != null &&
                            (result.title != null ? result.title : result.name)}
                    </Title>
                    <ItemContainer>
                        <Item>
                            {result != null &&
                                (result.known_for_department != null
                                    ? result.known_for_department
                                    : null)}
                        </Item>
                        {result != null &&
                            (result.place_of_birth != null ? (
                                <Divider>|</Divider>
                            ) : null)}
                        <Item>
                            {result != null &&
                                (result.place_of_birth != null
                                    ? result.place_of_birth
                                    : null)}
                        </Item>
                        {result != null &&
                            (result.imdb_id != null ? (
                                <Divider>|</Divider>
                            ) : null)}
                        <Item>
                            {result != null &&
                                (result.imdb_id != null ? (
                                    <a
                                        href={
                                            result != null &&
                                            (result.imdb_id != null
                                                ? `https://www.imdb.com/name/${result.imdb_id}`
                                                : null)
                                        }
                                    >
                                        <img
                                            class="fit-picture"
                                            src="https://m.media-amazon.com/images/G/01/IMDb/BG_rectangle._CB1509060989_SY230_SX307_AL_.png"
                                            alt="Grapefruit slice atop a pile of other slices"
                                            width="30px"
                                        ></img>
                                    </a>
                                ) : null)}
                        </Item>
                    </ItemContainer>

                    <Overview>
                        {result != null &&
                            (result.biography != null
                                ? result.biography
                                : null)}
                    </Overview>
                    {movieCredits != null &&
                        movieCredits.cast != null &&
                        movieCredits.cast.length > 0 && (
                            <>
                                <SectionTitle>Movies - Cast</SectionTitle>
                                <HR />
                                <Credits>
                                    {movieCredits.cast.map((movie) => {
                                        return (
                                            <Link to={`/movie/${movie.id}`}>
                                                <CreditContainer>
                                                    <CreditCover
                                                        poster={
                                                            movie.poster_path !=
                                                            null
                                                                ? `https://image.tmdb.org/t/p/w200${movie.poster_path}`
                                                                : 'data:image/png;base64,'
                                                        }
                                                    />
                                                    <CreditTitle>
                                                        {movie.title != null
                                                            ? movie.title
                                                                  .length > 15
                                                                ? `${movie.title.substring(
                                                                      0,
                                                                      15
                                                                  )}...`
                                                                : movie.title
                                                            : null}
                                                    </CreditTitle>
                                                    <CharacterName>
                                                        {movie.character != null
                                                            ? movie.character
                                                                  .length > 15
                                                                ? `${movie.character.substring(
                                                                      0,
                                                                      15
                                                                  )}...`
                                                                : movie.character
                                                            : null}
                                                    </CharacterName>
                                                </CreditContainer>
                                            </Link>
                                        );
                                    })}
                                </Credits>
                            </>
                        )}
                    {movieCredits != null &&
                        movieCredits.crew != null &&
                        movieCredits.crew.length > 0 && (
                            <>
                                <SectionTitle>Movies - Crew</SectionTitle>
                                <HR />

                                <Credits>
                                    {movieCredits.crew.map((movie) => {
                                        return (
                                            <Link to={`/movie/${movie.id}`}>
                                                <CreditContainer>
                                                    <CreditCover
                                                        poster={
                                                            movie.poster_path !=
                                                            null
                                                                ? `https://image.tmdb.org/t/p/w200${movie.poster_path}`
                                                                : 'data:image/png;base64,'
                                                        }
                                                    />
                                                    <CreditTitle>
                                                        {movie.title != null
                                                            ? movie.title
                                                                  .length > 15
                                                                ? `${movie.title.substring(
                                                                      0,
                                                                      15
                                                                  )}...`
                                                                : movie.title
                                                            : null}
                                                    </CreditTitle>
                                                    <CharacterName>
                                                        {movie.job != null
                                                            ? movie.job.length >
                                                              15
                                                                ? `${movie.job.substring(
                                                                      0,
                                                                      15
                                                                  )}...`
                                                                : movie.job
                                                            : null}
                                                    </CharacterName>
                                                </CreditContainer>
                                            </Link>
                                        );
                                    })}
                                </Credits>
                            </>
                        )}
                    {tvCredits != null &&
                        tvCredits.cast != null &&
                        tvCredits.cast.length > 0 && (
                            <>
                                <SectionTitle>TV Shows - Cast</SectionTitle>
                                <HR />

                                <Credits>
                                    {tvCredits.cast.map((show) => {
                                        return (
                                            <Link to={`/show/${show.id}`}>
                                                <CreditContainer>
                                                    <CreditCover
                                                        poster={
                                                            show.poster_path !=
                                                            null
                                                                ? `https://image.tmdb.org/t/p/w200${show.poster_path}`
                                                                : 'data:image/png;base64,'
                                                        }
                                                    />
                                                    <CreditTitle>
                                                        {show.name != null
                                                            ? show.name.length >
                                                              15
                                                                ? `${show.name.substring(
                                                                      0,
                                                                      15
                                                                  )}...`
                                                                : show.name
                                                            : null}
                                                    </CreditTitle>
                                                    <CharacterName>
                                                        {show.character != null
                                                            ? show.character
                                                                  .length > 15
                                                                ? `${show.character.substring(
                                                                      0,
                                                                      15
                                                                  )}...`
                                                                : show.character
                                                            : null}
                                                    </CharacterName>
                                                </CreditContainer>
                                            </Link>
                                        );
                                    })}
                                </Credits>
                            </>
                        )}
                    {tvCredits != null &&
                        tvCredits.crew != null &&
                        tvCredits.crew.length > 0 && (
                            <>
                                <SectionTitle>TV Shows - Crew</SectionTitle>
                                <HR />

                                <Credits>
                                    {tvCredits.crew.map((show) => {
                                        return (
                                            <Link to={`/show/${show.id}`}>
                                                <CreditContainer>
                                                    <CreditCover
                                                        poster={
                                                            show.poster_path !=
                                                            null
                                                                ? `https://image.tmdb.org/t/p/w200${show.poster_path}`
                                                                : 'data:image/png;base64,'
                                                        }
                                                    />
                                                    <CreditTitle>
                                                        {show.name != null
                                                            ? show.name.length >
                                                              15
                                                                ? `${show.name.substring(
                                                                      0,
                                                                      15
                                                                  )}...`
                                                                : show.name
                                                            : null}
                                                    </CreditTitle>
                                                    <CharacterName>
                                                        {show.job != null
                                                            ? show.job.length >
                                                              15
                                                                ? `${show.job.substring(
                                                                      0,
                                                                      15
                                                                  )}...`
                                                                : show.job
                                                            : null}
                                                    </CharacterName>
                                                </CreditContainer>
                                            </Link>
                                        );
                                    })}
                                </Credits>
                            </>
                        )}
                </Data>
            </Content>
        </Container>
    );
};
