import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const Image = styled.div`
    background-image: url(${(props) => `${props.bgUrl}`});
    height: 225px;
    width: 150px;
    background-size: cover;
    border-radius: 4px;
    background-position: center center;
    background-color: black;
    transition: opacity 0.2s linear;
`;

const Rate = styled.span`
    text-align: right;
    position: absolute;
    top: 215px;
    left: 100px;
    color: yellow;
    opacity: 0;
    font-weight: 700;
    transition: opacity 0.2s linear;
`;
const Title = styled.span`
    display: block;
    font-size: 12px;
    font-weight: 500;
    margin-bottom: 3px;
    height: 18px;
    width: 150px;
    text-align: center;
    margin: 0px;
    padding: 0px;
`;

const Year = styled.span`
    color: grey;
    margin: 0px;
    padding: 0px;
`;

const ImageContainer = styled.div`
    margin-bottom: 5px;

    position: relative;
`;
const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    padding: 0px 10px;
    &:hover {
        ${Image} {
            opacity: 0.7;
        }
        ${Rate} {
            opacity: 1;
        }
    }
`;

const Poster = ({ id, imgUrl, title, rating, year, isMovie = false }) => (
    <Link
        to={
            isMovie === 'movie'
                ? `/movie/${id}`
                : isMovie === 'show'
                ? `/show/${id}`
                : isMovie === 'collection'
                ? `/collection/${id}`
                : `/profile/${id}`
        }
    >
        <Container>
            <ImageContainer>
                <Image
                    bgUrl={
                        imgUrl != null
                            ? `https://image.tmdb.org/t/p/w200${imgUrl}`
                            : 'data:image/png;base64,'
                    }
                />
                {rating != null ? (
                    <Rate>{rating != null ? `${rating} / 10` : null} </Rate>
                ) : null}
            </ImageContainer>
            <Title>
                {title.length > 20 ? `${title.substring(0, 22)}...` : title}
            </Title>
            {year != null && <Year>{year}</Year>}
        </Container>
    </Link>
);

Poster.propTypes = {
    id: PropTypes.number.isRequired,
    imgUrl: PropTypes.string,
    title: PropTypes.string.isRequired,
    rating: PropTypes.number,
    year: PropTypes.string,
    isMovie: PropTypes.string,
};

export default Poster;
